/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.image {
  background-image: url(../images/pexels-rodnae-productions-7697272.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}

.bg-1 {
  background-color: rgba(0, 0, 0, 0.507);
  height: 100vh;
}

.text-centrado {
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
  flex-direction: column;
}
@media (min-width: 768px) {
  .text-centrado {
    height: 50rem;
  }
}

.text-white {
  color: #ffffff;
}

.fw-bold {
  font-weight: 600;
}

.d-flex {
  display: flex;
  gap: 1rem;
}

.align-center {
  justify-content: center;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  font-weight: 700;
}
@media (min-width: 768px) {
  .nombre-pagina {
    margin: 5rem 0;
  }
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}

.imagen {
  background-image: url(../images/1.jpg);
  background-size: cover;
  background-position: center center;
  height: 20rem;
}
@media (min-width: 768px) {
  .imagen {
    height: auto;
  }
}

.app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 700;
  border-radius: 0.3rem;
}
.alerta.error {
  background-color: #b21c3a;
}
.alerta.exito {
  background-color: #1c8b1c;
}

.formulario {
  width: 100%;
  margin-top: 3rem;
}

.campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.campo label {
  margin-bottom: 0.5rem;
}
.campo input {
  border: none;
  padding: 1.1111111111rem;
  border-radius: 0.5rem;
}

.btn-primary {
  background-color: #0da6f3;
  padding: 1.5rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 0.3rem;
}
@media (min-width: 768px) {
  .btn-primary {
    width: auto;
  }
}
.btn-primary:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.btn-secondary {
  background-color: #7a7575;
  padding: 1.5rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 0.3rem;
}
@media (min-width: 768px) {
  .btn-secondary {
    width: auto;
  }
}
.btn-secondary:hover {
  background-color: #605c5c;
  cursor: pointer;
}

.acciones {
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}